.event-list {
    overflow: hidden;
    transition: max-width 0.3s ease-out;
    max-width: 100%;
  }
  
  .event-list.expanded {
    max-width: 0;
  }
  
  .event-details {
    overflow: hidden;
    transition: max-width 0.3s ease-out;
    max-width: 0;
  }
  
  .event-details.expanded {
    max-width: 100%;
  }
  